body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  background: linear-gradient(135deg, #0f0c29, #302b63, #24243e);
  color: #fff;
  overflow-x: hidden;
}

.dashboard {
  padding: 30px;
}

.header {
  text-align: center;
  margin-bottom: 40px;
}

.header h1 {
  font-size: 2.4rem;
  color: #9f7aea;
  text-shadow: 0 0 10px #9f7aea;
}

.header p {
  color: #b3b3b3;
  font-size: 1rem;
}

.metrics {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.card {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 12px;
  text-align: center;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.4);
  transition: all 0.3s ease;
}

.card h2 {
  color: #e5e5e5;
}

.card p {
  font-size: 2rem;
  font-weight: bold;
  margin: 10px 0 0;
}

.card.total {
  border-left: 5px solid #a855f7;
}

.card.approved {
  border-left: 5px solid #22c55e;
}

.card.blocked {
  border-left: 5px solid #ef4444;
}

.card.pending {
  border-left: 5px solid #facc15;
}

.table-section {
  background: rgba(255, 255, 255, 0.05);
  padding: 25px;
  border-radius: 10px;
}

.table-section h2 {
  margin-bottom: 20px;
  color: #a855f7;
  text-shadow: 0 0 10px #a855f7;
}

.transaction-table {
  width: 100%;
  border-collapse: collapse;
}

.transaction-table th,
.transaction-table td {
  padding: 12px 16px;
  text-align: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.transaction-table th {
  color: #c084fc;
}

.approved-row {
  background: rgba(34, 197, 94, 0.1);
}

.blocked-row {
  background: rgba(239, 68, 68, 0.1);
}

.pending-row {
  background: rgba(250, 204, 21, 0.1);
}

.icon {
  font-size: 1.2rem;
}

.icon.approved {
  color: #22c55e;
}

.icon.blocked {
  color: #ef4444;
}

.icon.pending {
  color: #facc15;
}

.btn-group {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.btn {
  border: none;
  padding: 8px 14px;
  border-radius: 6px;
  font-weight: 600;
  cursor: pointer;
  color: white;
  transition: 0.3s ease;
}

.btn.approve {
  background: linear-gradient(to right, #22c55e, #16a34a);
}

.btn.approve:hover {
  background: linear-gradient(to right, #16a34a, #15803d);
}

.btn.block {
  background: linear-gradient(to right, #ef4444, #b91c1c);
}

.btn.block:hover {
  background: linear-gradient(to right, #b91c1c, #7f1d1d);
}

.done {
  color: #a1a1aa;
  font-style: italic;
}

.loading {
  text-align: center;
  padding: 20px;
  font-size: 1.2rem;
  color: #9f7aea;
}
